.site-list {
	display: flex;
	min-height: 100vh;
	.single-site {
		flex: 1 1 0;
		display: flex;
		justify-content: center;
		text-align: center;
		padding-block: 76px 61px;
		position: relative;
		background-color: #f3f1f1;

		&__wrapper {
			display: flex;
			flex-direction: column;
			align-items: center;
			max-width: 100%;
			padding-inline: 20px;
		}
		&__logo {
			display: flex;
			justify-content: center;
			align-items: center;
			background: #fff;
			width: 419px;
			max-width: 100%;
			height: 163px;
			border-radius: 5px;
			box-shadow: 0 10px 15px 0 rgba(0, 0, 0, 0.05);
			margin-bottom: 30px;
		}
		&__description {
			max-width: 419px;
			margin-bottom: 30px;
			display: flex;
			flex-direction: column;
			align-items: center;
			h2 {
				font-size: 32px;
				font-weight: bold;
				line-height: 1.34em;
				letter-spacing: normal;
				color: #5c5240;
				margin-bottom: 15px;
			}
			p {
				font-size: 15px;
				margin-bottom: 27px;
				line-height: 1.6em;
				max-width: 363px;
			}
			a {
				padding: 15px 28px;
				margin-bottom: 47px;
				background: #5c5240;
				color: #fff;
				font-size: 13px;
				font-weight: bold;
				line-height: 1.46em;
				box-shadow: 0 3px 19px 0 rgba(0, 0, 0, 0.3);
			}
		}
		&__images {
			display: flex;
			column-gap: 13px;
			width: 400px;
			max-width: 100%;
			img {
				flex: 1 1 0;
				display: block;
				min-width: 0;
				border-radius: 5px;
				max-height: 255px;
				object-fit: contain;
			}
		}

		&__verbiage {
			margin-top: 32px;
		}

		&.porch-store {
			background: #4d432f;
			color: #fff;
			h2 {
				color: #fff;
			}
			.single-site__description a {
				background: #fff;
				color: #4d432f;
			}

			.single-site__images {
				width: 566px;
				max-width: 100%;
			}
		}

		.site-link {
			position: absolute;
			inset: 0;
		}
	}

	@media screen and (max-width: 1200px) {
		flex-direction: column-reverse;
	}
}
